@charset "UTF-8";
.fl-fr {
  display: flex;
  flex-flow: row wrap;
  align-items: flex-start;
  font-family: helvetica, arial, sans-serif;
  font-size: 14px;
  line-height: 1.38em;
  justify-content: center;
  text-align: left;
  max-width: 750px;
  margin: auto; }

.fl-fr_postsContainer {
  flex: 2 2 350px;
  margin: 6px; }

.fl-fr_leftColumn {
  flex: 1 2 210px;
  margin: 6px; }

.fl-fr_facebookLogoBar {
  display: block;
  background-color: #4254a0;
  margin: 0 0 6px;
  padding: .4em 1em .05em;
  text-decoration: none; }
  .fl-fr_facebookLogoBar > svg {
    width: 1.6em;
    height: 1.6em; }

.fl-fr_reviewsBar {
  position: relative;
  display: block;
  background-color: #e5e6ea;
  color: #4b4f56;
  margin: 6px 0;
  padding: 0.8em 11.9px;
  text-transform: uppercase;
  border: 1px solid #d0d1d5;
  font-size: 9.52px;
  line-height: 1.34em;
  text-decoration: none; }
  .fl-fr_reviewsBar span {
    transform: rotateZ(-90deg);
    position: absolute;
    right: 0;
    top: .3em; }
  .fl-fr_reviewsBar path {
    fill: #4b4f56; }

.fl-fr_Post-post,
.fl-fr_Post-header,
.fl-fr_Post-header-image,
.fl-fr_Post-header-text,
.fl-fr_Post-header-text-top,
.fl-fr_Post-header-text-top-name,
.fl-fr_Post-header-text-top-actionTarget,
.fl-fr_Post-header-text-stars,
.fl-fr_Post-header-text-bottom,
.fl-fr_Post-icon icon-world,
.fl-fr_Post-ExpansibleTextBox,
.fl-fr_Post-ExpansibleTextBox-seeMore {
  margin: 0;
  padding: 0;
  font-size: inherit;
  font-family: inherit;
  text-decoration: none;
  line-height: inherit; }

.fl-fr_Post {
  display: block;
  padding: 11.9px;
  margin: 0 0 10px;
  background-color: #fff;
  border: 1px solid #d0d1d5; }

.fl-fr_Post-header {
  color: #90949c;
  display: flex;
  flex-flow: row nowrap;
  align-items: stretch;
  position: relative;
  padding-bottom: 6px; }

.fl-fr_Post-header-image {
  width: 40px;
  height: 40px;
  display: block;
  padding-right: 8px; }

.fl-fr_Post-header-text {
  display: flex;
  flex-flow: column nowrap;
  align-items: stretch; }

.fl-fr_Post-header-text-top {
  margin-bottom: 2px; }

.fl-fr_Post-header-text-top-name {
  font-weight: bold;
  color: #4254a0; }

.fl-fr_Post-header-text-top-actionTarget {
  color: #4254a0; }

.fl-fr_Post-header-text-stars {
  color: #fff;
  background-color: #558eff;
  border-radius: 1em;
  vertical-align: middle;
  line-height: 1.3em;
  padding: 0.3em .7em;
  margin-bottom: .2em;
  display: inline-block;
  font-size: .7em; }
  .fl-fr_Post-header-text-stars:after {
    content: '★';
    font-size: 1em;
    margin-left: -.2em;
    margin-bottom: .4em; }

.fl-fr_Post-header-text-bottom {
  font-family: inherit;
  font-size: 11.9px;
  line-height: 1.34em; }

.fl-fr_Post-header-arrowDown, .fl-fr_Post-header-arrowDown * {
  display: block;
  fill: #90949c;
  position: absolute;
  top: -2px;
  right: 0;
  font-size: 28px; }

.fl-fr_Post-header-text-globe,
.fl-fr_Post-header-text-globe * {
  fill: #90949c; }

.fl-fr_Post-ExpansibleTextBox {
  display: block;
  color: #1d2129;
  font-size: 14px;
  line-height: 1.38em; }

.fl-fr_Post-ExpansibleTextBox-seeMore {
  color: #4254a0;
  display: inline-block;
  cursor: pointer;
  margin-left: .3em; }

.fl-fr_ScoreBoard,
.fl-fr_ScoreBoard-header,
.fl-fr_ScoreBoard-header-mainScore,
.fl-fr_ScoreBoard-header-scoreDetails,
.fl-fr_ScoreBoard-header-scoreDetails-starCount,
.fl-fr_ScoreBoard-header-scoreDetails-reviewCount,
.fl-fr_ScoreBoard-body,
.fl-fr_ScoreBoard-body-reviewCountStarScore,
.fl-fr_ScoreBoard-body-reviewCountStarScore-stars,
.fl-fr_ScoreBoard-body-reviewCountStarScore-reviews,
.fl-fr_ScoreBoard-body-reviewCountStarScore,
.fl-fr_ScoreBoard-body-reviewCountStarScore-stars,
.fl-fr_ScoreBoard-body-reviewCountStarScore-reviews,
.fl-fr_ScoreBoard-body-reviewCountStarScore,
.fl-fr_ScoreBoard-body-reviewCountStarScore-stars,
.fl-fr_ScoreBoard-body-reviewCountStarScore-reviews,
.fl-fr_ScoreBoard-body-reviewCountStarScore,
.fl-fr_ScoreBoard-body-reviewCountStarScore-stars,
.fl-fr_ScoreBoard-body-reviewCountStarScore-reviews,
.fl-fr_ScoreBoard-body-reviewCountStarScore,
.fl-fr_ScoreBoard-body-reviewCountStarScore-stars,
.fl-fr_ScoreBoard-body-reviewCountStarScore-reviews {
  margin: 0;
  padding: 0;
  font-size: inherit;
  font-family: inherit;
  text-decoration: none;
  line-height: inherit; }

.fl-fr_ScoreBoard {
  background-color: white;
  padding: 15.96px;
  border: 1px solid #d0d1d5; }

.fl-fr_ScoreBoard-header {
  display: flex;
  flex-flow: row nowrap;
  padding-bottom: 15.96px;
  border-bottom: 1px solid #d0d1d5; }

.fl-fr_ScoreBoard-header-mainScore {
  color: #fff;
  background-color: #558eff;
  border-radius: 1em;
  vertical-align: middle;
  line-height: 1.3em;
  padding: 0.3em .7em;
  margin-bottom: .2em;
  display: inline-block;
  font-size: 20px;
  margin-right: .5em; }
  .fl-fr_ScoreBoard-header-mainScore:after {
    content: '★';
    font-size: 1em;
    margin-left: -.2em;
    margin-bottom: .4em; }

.fl-fr_ScoreBoard-header-scoreDetails {
  font-size: 11.9px;
  line-height: 1.34em;
  color: #4b4f56;
  padding-top: .2em; }

.fl-fr_ScoreBoard-header-scoreDetails-starCount,
.fl-fr_ScoreBoard-header-scoreDetails-reviewCount {
  display: block; }

.fl-fr_ScoreBoard-body {
  color: #90949c;
  padding-top: 15.96px; }

.fl-fr_ScoreBoard-body-reviewCountStarScore {
  display: flex;
  flex-flow: row nowrap; }

.fl-fr_ScoreBoard-body-reviewCountStarScore-stars {
  padding-right: 1em;
  font-size: 11.9px; }
  .fl-fr_ScoreBoard-body-reviewCountStarScore-stars::after {
    content: ' stars'; }

.fl-fr_ScoreBoard-body-reviewCountStarScore-bar {
  content: ' ';
  display: inline-block;
  min-width: .5em;
  height: .5em;
  background-color: #558eff;
  border-radius: .5em;
  margin-right: 8px;
  margin-top: .4em; }

.fl-fr_ScoreBoard-body-reviewCountStarScore-stars {
  white-space: nowrap; }
